i {
    color: rgb(218, 169, 79);
    cursor: pointer;
}

.hoverEffect {
    filter: hue-rotate(45deg);
}

#modalTable {
    width: 100%;
}

.modalSelectStyle {
    width: 100%;
    font-size: 0.9rem;
    padding: 5px;
}

.chess-cell {
    cursor: pointer;
}

.rightClickEffect {
    background-color: red;
}